import React, { Component } from 'react'
import { connect } from 'react-redux'
import { socketService } from '../service/socketService'
import Avatar from '@material-ui/core/Avatar';

class _Chat extends Component {
  state = {
    msg: { txt: '' },
    msgs: [],
    isBotMode: true,
    currTypingUser: {
      name: 'donald_trump',
      imgUrl: 'https://besttv232-ynet-images1-prod.cdn.it.best-tv.com/PicServer5/2017/08/29/7999138/7999115010001009801066no.jpg',
      isTyping: false
    }
  }

  componentDidMount() {
    const savedMsgs = socketService.getMsgsFromStorage() || [];
    this.setState({ msgs: savedMsgs });

    socketService.setup()
    socketService.emit('chat topic', 'abraham_licolni')

    socketService.on('chat addMsg', this.addMsg)

    socketService.on('userTyping', this.setUserTyping)
  }

  componentDidUpdate(prevProps) {
    const didUserChange = 'abraham_licolni' !== 'abraham_licolni';

    if (didUserChange) {
      socketService.emit('chat topic', 'abraham_licolni')
    }
  }

  componentWillUnmount() {
    socketService.off('chat addMsg', this.addMsg)
    socketService.off('userTyping', this.setUserTyping)

    socketService.terminate()
    clearTimeout(this.timeout)
  }


  setUserTyping = ({ username, msg }) => {

    this.setState({ currTypingUser: { username, isTyping: !!msg } }, () => {
      console.log('currTypingUser: ', this.state.currTypingUser);
    })
  }


  addMsg = newMsg => {
    this.setState(prevState => ({ msgs: [...prevState.msgs, newMsg] }), () => {

      socketService.saveMsgsToStorage(this.state.msgs)
    })
    if (this.state.isBotMode) this.onSendBotResponse();
  }

  onSendBotResponse = () => {
    // Handle case: send single bot response (debounce).
    this.timeout && clearTimeout(this.timeout)

    this.timeout = setTimeout(this.sendBotResponse, 1500)
  }

  sendBotResponse = () => {
    this.setState(prevState => ({
      msgs: [...prevState.msgs, {
        txt: 'hi there, long time no see!'
      }]
    }), () => {
      socketService.saveMsgsToStorage(this.state.msgs)
    })
  }

  sendMsg = ev => {
    ev.preventDefault()

    socketService.emit('chat newMsg', { txt: this.state.msg.txt })
    this.setState({ msg: { txt: '' } })
  }

  msgHandleChange = async ev => {
    const { name, value } = ev.target
    const user = {
      username: 'abraham_lincoln',
      msg: value
    }

    //   username: this.props.loggedinUser?.fullname || this.props.loggedinUser?.username || 'guest',
    //   msg: value

    socketService.emit('typing', user)

    this.setState(prevState => {
      return {
        msg: {
          ...prevState.msg,
          [name]: value
        }
      }
    })
  }

  render() {
    const { currTypingUser, msg } = this.state
    const { isOpen } = this.props
    return (
      <section className="chat-cmp">
        <div className={`chat ${isOpen && 'active'}`}>
          <h2 className="chat-topic chat-layout flex space-between">Massenger<i className="fas fa-times" onClick={this.props.onClose}></i></h2>
          <label>
            <Avatar aria-label="recipe" className={"avatar"}>
              <img src={currTypingUser.imgUrl} />
            </Avatar>
            <input
              type="checkbox"
              name="isBotMode"
              checked={this.state.isBotMode}
              onChange={(ev) => this.setState({ isBotMode: ev.target.checked })}
              className="chat-layout"
            />

          </label>
          <h3>{currTypingUser?.isTyping && currTypingUser.username + ' is typing...'}</h3>

          <div className="chat-main-content">
            <ul className="chat-msgs chat-layout">
              {this.state.msgs.map((msg, idx) => (
                <li key={idx}>
                  <Avatar aria-label="recipe" className={"avatar"}>
                   <img src={currTypingUser.imgUrl} />
                   </Avatar>
                    {msg.txt}
                    </li>
              ))}
            </ul>
            <form className="msg-form" onSubmit={this.sendMsg}>
              <input
                type="text"
                value={msg.txt}
                onChange={this.msgHandleChange}
                name="txt"
                className="msg-input"
                autoComplete="off" />
              <button>Send</button>
            </form>
          </div>
        </div>
      </section>
    )
  }
}

const mapStateToProps = state => {
  return {
    loggedinUser: 'state.user.loggedinUser'
  }
}
const mapDispatchToProps = {
}

export const Chat = connect(mapStateToProps, mapDispatchToProps)(_Chat)
