import { React, Component } from 'react'
import { connect } from 'react-redux'
import { addComment } from '../store/action/commentActions.js'
import { utilService } from '../service/utilService.js'
import Avatar from '@material-ui/core/Avatar';

class _AddComment extends Component {

    state = {
        comment: {
            _id: utilService.makeId(),
            txt: "",
            byUser: {
                _id: "sadad748",
                username: "abraham_lincoln",
                imgUrl: "https://www.goodesign.co.il/wp-content/uploads/2017/03/HIPSTORY-Shimoni-Lincoln.jpg"
            },
            createdAt: Date.now()
        }
    }


    onSaveComment = async (ev) => {
        ev.preventDefault()
        const savedComment = this.state.comment
        if (!savedComment.txt) return alert('Can not upload empty comment')
        this.props.addComment(savedComment, this.props.post._id)
        this.setState({
            comment: {
                _id: utilService.makeId(),
                txt: "",
                byUser: {
                    _id: "sadad748",
                    username: "abraham_lincoln",
                    imgUrl: "https://www.goodesign.co.il/wp-content/uploads/2017/03/HIPSTORY-Shimoni-Lincoln.jpg"
                },
                createdAt: Date.now()
            }
        })
    }

    onInputChange = (ev) => {
        const comment = { ...this.state.comment };
        comment.txt = ev.target.value;
        this.setState({
            comment: comment
        });
    };

    render() {
        return (
            <section className="comment-area">
                <form onSubmit={this.onSaveComment}>
                    <Avatar aria-label="recipe" className={"avatar"}>
                        <img src={this.state.comment.byUser.imgUrl} />
                    </Avatar>
                    <input value={this.state.comment.txt} type="txt" placeholder="Add a comment..." name="comment" className="add-comment-textarea" onChange={this.onInputChange} />
                    <button className="add-comment-btn">Post</button >
                </form>

            </section>
        )


    }
}
const mapStateToProps = state => {
    return {
        comments: state.postModule.comments
    }
}

const mapDispatchToProps = {
    addComment
}

export const AddComment = connect(mapStateToProps, mapDispatchToProps)(_AddComment)


