import { postService } from "../../service/postService.js"

export function loadComments(filterBy) {
    return (dispatch) => {
        postService.query(filterBy)
            .then(comments => { dispatch({ type: 'SET_COMMENTS', comments }) })
    }
}

export function removeComment(commentId, postId) {
    return async (dispatch) => {
        const post = await postService.getById(postId)
        post.comments = post.comments.filter(comment => comment._id !== commentId)
        await postService.savePost(post)
        dispatch({ type: 'EDIT_POST',post })
    }
}

export function addComment(comment, postId) {
    return async (dispatch) => {
        const post = await postService.getById(postId)
        post.comments.push(comment)
        postService.savePost(post).then((post) => {
            dispatch({ type: 'EDIT_POST', post })
        })
    }
}

