import { CommentPreview } from "./CommentPreview.jsx"

export function CommentList({ comments,removeComment,post,loggedinUser }) {
    return (
        <div className="comment-list">
            {comments.map(comment => {
                return <CommentPreview key={comment._id} comment={comment} removeComment={removeComment} post={post} loggedinUser={loggedinUser} />
            })}
        </div>
    )
}