import React from 'react';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemText from '@material-ui/core/ListItemText';
import CardHeader from '@material-ui/core/CardHeader';
import ListItemAvatar from '@material-ui/core/ListItemAvatar';
import Avatar from '@material-ui/core/Avatar';
import { utilService } from '../service/utilService.js';
import IconButton from '@material-ui/core/IconButton';
import DeleteIcon from '@material-ui/icons/Delete';

export function CommentPreview({ comment, removeComment, post, loggedinUser }) {

    return (
        <section>
            <List className={"root"}>
                <ListItem>
                    <ListItemAvatar>
                        {comment.byUser.imgUrl && <Avatar aria-label="recipe" className={"avatar"}>
                            <img src={comment.byUser.imgUrl} />
                        </Avatar>}
                    </ListItemAvatar>
                    <ListItemText primary={comment.byUser.username} secondary={comment.txt} />
                    {loggedinUser._id === comment.byUser._id && <div className="delete-icon" onClick={() => removeComment(comment._id, post._id)}>
                        <IconButton aria-label="delete" disabled color="primary">
                            <DeleteIcon />
                        </IconButton>
                    </div>}
                    <CardHeader subheader={utilService.timeDifference(Date.now(), comment.createdAt)} />
                </ListItem>
            </List>
        </section >
    );
}








